<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="banben">
           <span><img src="../assets/images/logo.png"></span>
           <span>超维 {{taotalnum}}</span>
        </div>
        <div class="pro_list">
            <yd-flexbox class="pro_list_item" v-for="item in list">
                <yd-flexbox-item  class="pro_list_img">
                    <span>{{item.currName}}</span>
                </yd-flexbox-item>
                <yd-flexbox-item  class="pro_list_price">
                    <span>{{item.num}}</span>
                    <span>~ {{item.coinprice}}</span>
                </yd-flexbox-item>
            </yd-flexbox>
        </div>
    </div>
  
</template>

<script>
  import { getTokenQty } from '../assets/js/Keytoken.js'
  import {getCookie,setCookie,getCookieWithDefault} from '../assets/js/cookie.js'
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.developers'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      content:'',
      list:[],
      taotalnum:0
    }
  },
  created(){
    this.listdata()
  },
  methods:{
    listdata(){
      var that = this
      that.axios({
        url:that.Url + 'ShowCurrency.ashx',
        method:'post',
        headers:{'Content-Type':'application/x-www-form-urlencoded'},
        params: {req:4,publickey:getCookie('publickey'),moneycode: getCookieWithDefault('coin','CNY')}
      }).then(function (res) {
        if(res.data.success){
          var arry = res.data.data
          arry.forEach(item => {
            item.num = "- -"
            item.coinprice = "- -"
          });
          that.list = arry
          return Promise.resolve(res.data)
        }
      }).then(function(data){
        that.test(data.data,0)
      })
    },
    test(_list,a){
      var that = this
      var arr = []
      if(a<_list.length)
      {
          _list[a].coinprice=0
          _list[a].num=0
          getTokenQty(getCookie('publickey'),_list[a].contractAddress,_list[a].currName,_list[a],function(obj){
              that.total(_list)
              that.test(_list,++a);
          })
      } 
    },
    total(item){
        var that = this
        that.taotalnum=0
        if(item){
            for (var i = 0; i < item.length; i++) {
                if(item[i].coinprice != ""){
                    that.taotalnum += parseFloat(item[i].coinprice)
                }
            }
        }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->


<style scoped>

.yingyong{
  padding-top: .9rem
}
.softapp{
  padding: 0.5rem;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%
}
.comname{
  font-size: .4rem!important;
  color: #333;
  text-shadow: 5px 2px 1px #ccc;
  letter-spacing: 5px;
  margin-bottom: .2rem;
  text-align: center;
}
.comeng,.comhttp{
  text-shadow: 2px 2px 1px #ccc;
  font-size: .3rem;
  color: #666;
  text-align: center;
}
.yd-cell-item{
  border-bottom:1px solid #f4f4f4;
}
.yd-cell-item span:nth-child(2){
  font-size: .3rem;
  color: #333;
}
.banben{
  padding: .8rem .5rem;
  text-align: center;
}
.banben span:nth-child(1){
  display: block;
  width: 2rem;
  height: 2rem;
  /*background: #ff6767;*/
  margin: 0 auto .3rem;
}
.banben span:nth-child(1) img{
  width: 100%
}
.banben span:nth-child(2){
  font-size: .26rem;
  color: #666;
}
.banben p{
  text-align: justify;
  line-height: 1.5;
  padding-top: .5rem;
  font-size: .28rem;
}
</style>
<style>
@import '../assets/css/index.css'
</style>


